.wishlist {
  &-book {
    display: flex;
    border: 1px solid gray;
    border-radius: 8px;
    padding: 15px 30px;
    width: 100%;
    margin: 10px 0;

    &-link {
      text-decoration: none !important;

      &-img {
        height: 160px;

        &-div {
          width: 140px;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }

      &-title {
        max-height: 55px;
        overflow: hidden;
      }

      &-div {
        margin: 10px;
      }
    }
  }

  &-books-map {
    padding: 20px;
  }
}

@media (max-width: 800px) {
  .wishlist-book-link {
    &-title {
      font-size: 1.2rem;
    }

    &-author {
      font-size: 0.9rem;
    }
  }
}

@media (max-width: 600px) {
  .wishlist-book-link {
    &-title {
      font-size: 1rem;
    }

    &-author {
      font-size: 0.8rem;
    }
  }
}

@media (max-width: 500px) {
  .wishlist-book {
    padding: 5px 10px;

    &-link {
      &-img {
        height: 120px;
        &-div {
          width: 100px;
        }
      }

      &-price {
        margin: 0;
      }
    }
  }
}
