.table {
  &-row {
    &-book {
      &-div {
        display: flex;
      }
      &-link {
        text-decoration: none !important;
      }

      &-image {
        height: 100px;

        &-div {
          display: flex;
          width: 80px;
          justify-content: center;
        }
      }

      &-pres {
        margin-left: 10px;
      }

      &-title {
        color: black;
      }
    }
  }

  &-info {
    background-color: transparent !important;
    padding: 30px 0;
    text-align: center;
  }
}

.cart-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &-total {
    margin: 0 10px;
    font-size: 20px;
  }

  &-error {
    float: right;
    margin: 5px 15px;
    color: red;
  }
}
