.book {
  &-presentation {
    display: flex;
    width: 100%;
    padding: 20px 30px 40px;
    border-bottom: 1px solid gray;

    &-rating {
      outline: none !important;
    }

    &-image {
      height: 500px;
      margin: 0 auto;
    }

    &-info {
      min-width: 20vw;
      margin: 20px 10px;

      &-price {
        margin-top: 20px;
        color: #f56f02;
      }
    }

    &-buttons {
      width: 70%;
      min-width: 250px;
      margin: 10px 0;

      &-add {
        &-cart {
          display: block;
          width: 100%;
          height: 40px;
          margin: 0 !important;
        }

        &-wishlist {
          margin: 10px 0 !important;
          height: 40px;
          width: 100%;
          background-color: white !important;
          color: #007bff !important;

          &:hover {
            color: white !important;
            background-color: #007bff !important;
          }
        }
      }
    }
  }

  &-description {
    display: block;

    padding: 20px;
    border-bottom: 1px solid gray;

    &-title {
      margin-left: 15px;
      margin-bottom: 10px;
    }

    &-text {
      text-indent: 30px;
      line-height: 30px;
    }
  }

  &-details {
    // margin: 20px;
    padding: 20px;
    border-bottom: 1px solid gray;

    &-title {
      margin-left: 15px;
      margin-bottom: 20px;
    }

    &-spec {
      display: flex;

      th,
      td {
        border-top: none !important;
      }

      &-item {
        display: block;
        width: 150px;
      }
    }
  }

  &-reviews {
    margin: 20px;

    &-title {
      margin-left: 15px;
      margin-bottom: 20px;
    }
  }

  &-wishlist {
    position: absolute;
    border-radius: 4px;
    background-color: white;
    width: 90%;
    max-width: 600px;
    padding: 20px;
    border: 1px solid black;
    opacity: 1 !important;
    top: 25vh;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;

    &-top {
      display: flex;
      justify-content: space-between;
      padding-bottom: 20px;

      &-button {
        border: none;

        margin: 0;
        text-decoration: none;
        background-color: transparent;
        font-size: 24px;
        color: grey;
        text-align: center;
        outline: none;

        &:hover {
          color: #4a4a4a;
        }

        &:focus {
          background: transparent;
          outline: none;
          color: black;
        }
      }
    }

    &-form {
      padding: 10px;

      &-button {
        margin: 0 10px;
      }

      &-label {
        margin-left: 10px;
      }

      &-input {
        border-radius: 4px;
      }
    }
  }
}

@media (max-width: 680px) {
  .book-presentation {
    display: block;

    &-image {
      display: block;
      margin: 0 auto;
      height: 350px;
    }
  }
}

@media (max-width: 850px) {
  .book-presentation {
    padding: 20px 5px;
    &-image {
      display: block;
      margin: 0 auto;
      height: 400px;
    }

    &-info-title {
      font-size: 19px;
    }

    &-info-author {
      font-size: 16px;
    }

    &-buttons {
      width: 100%;
      margin: 0 auto;
      //  &-add {
      //   &-cart {
    }
  }
}

@media (min-width: 1000px) {
  .book-parent {
    width: 90%;
    margin: 0 auto;
  }
}
