.book {
  width: 180px;
  margin: 10px;
  text-decoration: none;
  text-align: center;
  justify-content: flex-start;
  display: block;
  height: 350px;

  &-infos {
    display: flex;
    justify-content: space-between;
    padding: 0 40px 0 5px;

    p {
      margin: 0;
    }

    button {
      align-self: flex-end;
    }
  }

  &-link {
    text-decoration: none !important;
    color: black;

    &-button {
      float: bottom;
    }

    &-div {
      margin: 10px;

      &-button {
        max-height: 40px;
      }
    }

    &-img {
      height: 140px;
    }

    &-title {
      font-size: 16px;
      height: 57px;
      overflow: hidden;
    }

    &-author {
      font-size: 14px;
      height: 34px;
      overflow: hidden;
    }
  }

  &-wishlist {
    display: flex;
    padding: 10px;
    margin: 10px;
    width: 100%;
    margin: 0 auto;
  }

  &-pages {
    text-align: center;

    &-button {
      text-decoration: none !important;
      color: black;
      font-size: 24px;
      padding: 5px;
      margin: 3px;
      border: 1px solid gray;
    }
  }
}

.books {
  padding: 0 10px;

  display: flex;

  &-map {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 100% !important;

    &::after {
      content: "";
      flex: auto auto 1;
    }
  }

  &-parent {
    margin: 15px 0;
    display: flex;
    flex-wrap: wrap;
  }
}

@media (max-width: 768px) {
  .books {
    display: block;
    padding: 0;

    &-parent {
      margin: 0;
    }
  }
}

@media (max-width: 500px) {
  .books {
    &-map {
      padding: 0 !important;
    }
  }

  .book {
    max-width: 45% !important;
    margin: 5px;
  }
}
