.order {
  &-books {
    &-title {
      margin-bottom: 30px;
    }

    &-map {
      display: flex;
      justify-content: flex-start;
    }

    &-item {
      display: flex;
      align-items: center;
      width: 160px;
      flex-wrap: wrap;

      &-multiple {
        margin-right: 8px;
      }
    }
  }
  &-cancel {
    margin: 50px 5px;
  }
}
