table {
  text-align: center;
}

.order {
  &-link {
    text-decoration: none !important;
    color: black;
  }

  &-buttons {
    border: none;
    background-color: transparent;
    color: gray;
    font-size: 24px;
    padding: 0;
    margin: 1px 6px;

    &:focus {
      outline: none;
    }

    &-cancel:hover {
      color: red;
    }

    &-delivered:hover {
      color: green;
    }
  }

  &-column-id {
    text-align: left;
    width: 220px !important;
  }
}

.table {
  th,
  td {
    vertical-align: middle !important;
  }
}
